/* Reset some default styles */
body,ul,li,h1,h2,h3,p {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  background: linear-gradient(250deg, #00e1ff 0%, #000000 100%);
  background-attachment: fixed;
  color: #fff;
}

#back_to{
  margin-top: 6%;
  margin-left: 40%;
  color: rgb(95, 221, 95);
  border-color: none;
  text-decoration: underline;
}


nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: black;
  color: white;
  border-radius: 20px;
  border-bottom: 2px solid yellow;/* Change to your preferred border color */
}


.logo-container {
  display: flex; 
  align-items: center;
  color: rgb(255 112 0);
  margin-left: inherit;
  border-radius: 77px;
  border-left: 2px solid rgb(213, 175, 104) ;
  border-right: 2px solid rgb(213, 175, 104) ;
}

.logo-container h1 {
  margin-right: 0.5rem;
  position: relative;
}

.menu-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
  color: white;
}

.menu-toggle span {
  height: 2px;
  width: 25px;
  background-color: black;
  margin: 6px 0;
  transition: 0.5s;
  background-color: white;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
}
.nav-links a.active {
  color: yellow;
  font-size: 27px;
  text-decoration: underline ;
}

.nav-links a {
  text-decoration: none;
  color: rgb(0, 250, 0);  
  font-weight: bold;
  font-size: 12px;
  background-color: black;
  border: none;
}


/* Media query for responsiveness */
@media (max-width: 769px) {
  .menu-toggle {
    display: flex;
    color: #fff;
  }

  .nav-links {
    flex-direction: column;
    position: absolute;
    top: 123px;
    left: 23%;
    width: 100%;
    text-align: center;
    display: none;
    border: none;
    clip-path:circle();
    background-color: transparent;
  }

  .nav-links.open {
    display: flex;
  }

  .menu-toggle.open span:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .menu-toggle.open span:nth-child(2) {
    opacity: 0;
  }

  .menu-toggle.open span:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
  }

  input {
    padding: 8px;
    margin-bottom: 20px;
    border-radius: 25px;
    background-color: black;
    font-size: 20px;
    margin-left: 30%;
    color: white;
    border-color: #fff;
  }


}

